[data-tabs] {
	overflow: hidden;
	[data-tabs-control] {
		cursor: pointer;

		&[disabled],
		&.is-disabled {
			cursor: not-allowed;
			@include nosel; } }
	[data-tabs-wrapper] {
		position: relative; }
	[data-tabs-container] {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.7s, visibility 0.7s;
		&.is-open {
			position: relative;
			opacity: 1;
			visibility: visible; } }
	.slick-slide {
		[data-tabs-container] {
			position: relative;
			visibility: visible;
			opacity: 1; } } }

.tabs {
	padding-top: 7px;
	.section {
		position: relative;
		z-index: 1;
		padding: 91px 0 40px;
		@include md {
			padding: 60px 0 20px; }
		&__img {
			margin-top: 25px;
			margin-right: -76px;
			@include sm {
				margin: 0 -30px 0; }
			img {
				margin: 0 0 0 auto;
				@include md {
					margin: 0 auto; } } }
		&__col_img {
			@include md {
				margin-bottom: 10px; }
			@include sm {
				margin-bottom: 0; } } }
	.slick-dots {
		position: absolute;
		top: -46px;
		left: 0;
		width: 100%;
		font-size: 0;
		padding: 0 30px;
		text-align: center;
		justify-content: space-around;
		display: flex;
		@include sm {
			padding: 0;
			display: block; }
		li {
			vertical-align: top;
			display: inline-block;
			&:not(:first-child) {
				margin-left: 34px; }
			&:first-child button {
				border-color: #D65151;
				color: #D65151; }
			&:nth-child(2) button {
				border-color: #AB6DA5;
				color: #AB6DA5; }
			&:nth-child(3) button {
				border-color: #826CE7;
				color: #826CE7; }
			&:nth-child(4) button {
				border-color: #7D89E9;
				color: #7D89E9; }
			&:nth-child(5) button {
				border-color: #78A7EB;
				color: #78A7EB; }
			&.slick-active {
				button {
					&:after {
						transform: scale(1); } } } }
		button {
			position: relative;
			@include circle(16);
			font-size: 0;
			border: 2px solid #D65151;
			color: #D65151;
			background-color: $white;
			&:after {
				position: absolute;
				content: '';
				top: 50%;
				left: 50%;
				margin: -3px 0 0 -3px;
				@include circle(6);
				transform: scale(0);
				transition: transform .3s;
				background-color: currentColor; } } }
	&__theme {
		position: relative;
		z-index: 1;
		text-align: center;
		background-color: #F9F9F9;
		display: none;
		@include md {
			display: block; } }
	&__theme-inner {
		padding: 11px 38px 10px;
		margin: 0 30px;
		min-height: 70px;
		@include hss;
		@include f(16px,400,48px);
		vertical-align: top;
		border-radius: 15px 15px 0 0;
		display: inline-block;
		span {
			line-height: 1.5;
			vertical-align: middle;
			display: inline-block; }
		[data-tabs-container="investing"] & {
			color: #D65151;
			background-color: rgba(#D65151, .07); }
		[data-tabs-container="investement"] & {
			color: #AB6DA5;
			background-color: rgba(#AB6DA5, .07); }
		[data-tabs-container="tools"] & {
			color: #826CE7;
			background-color: rgba(#826CE7, .07); }
		[data-tabs-container="custodianship"] & {
			color: #7D89E9;
			background-color: rgba(#7D89E9, .07); }
		[data-tabs-container="electronic"] & {
			color: #78A7EB;
			background-color: rgba(#78A7EB, .07); } }
	&__line {
		margin: 0 -193px 38px;
		@include md {
			margin: 0 -30px 37px; }
		img {
			margin: 0 auto;
			max-width: 100%;
			min-width: 100%;
			display: block;
			@include md {
				height: 2px; } } }
	&__list {
		padding: 0 31px;
		justify-content: space-around;
		display: flex; }
	&__item {
		position: relative;
		padding: 10px 37px;
		@include hss;
		@include f(16px,400,48px);
		letter-spacing: .02em;
		text-align: center;
		user-select: none;
		transition: color .3s, background-color .3s, opacity .3s;
		border-radius: 15px 15px 0 0;
		&:before {
			position: absolute;
			content: '';
			top: -46px;
			left: 50%;
			margin-left: -8px;
			@include circle(16);
			border: 2px solid;
			background-color: $white; }
		&:after {
			position: absolute;
			content: '';
			top: -41px;
			left: 50%;
			margin-left: -3px;
			@include circle(6);
			transform: scale(0);
			transition: transform .3s;
			background-color: currentColor; }
		&:not(.is-active) {
			@include hover {
				span {
					opacity: .7; } } }
		&[data-tabs-control="investing"] {
			&:before,
			&:after {
				color: #D65151; } }
		&[data-tabs-control="investement"] {
			&:before,
			&:after {
				color: #AB6DA5; } }
		&[data-tabs-control="tools"] {
			&:before,
			&:after {
				color: #826CE7; } }
		&[data-tabs-control="custodianship"] {
			&:before,
			&:after {
				color: #7D89E9; } }
		&[data-tabs-control="electronic"] {
			&:before,
			&:after {
				color: #78A7EB; } }
		&.is-active {
			color: $red;
			background-color: rgba(#D65151, .07);
			&:after {
				transform: scale(1); }
			&[data-tabs-control="investement"] {
				color: #AB6DA5;
				background-color: rgba(#AB6DA5, .07); }
			&[data-tabs-control="tools"] {
				color: #826CE7;
				background-color: rgba(#826CE7, .07); }
			&[data-tabs-control="custodianship"] {
				color: #7D89E9;
				background-color: rgba(#7D89E9, .07); }
			&[data-tabs-control="electronic"] {
				color: #78A7EB;
				background-color: rgba(#78A7EB, .07); } }
		span {
			line-height: 1.5;
			transition: opacity .3s;
			vertical-align: middle;
			display: inline-block; } }
	&__container {
		position: relative;
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			opacity: 0.07;
			pointer-events: none;
			background-image: linear-gradient(180deg, #D65151 0%, #F9F9F9 100%); }
		&[data-tabs-container="investement"] {
			&:before {
				background-image: linear-gradient(180deg, #AB6DA5 0%, #F9F9F9 100%); } }
		&[data-tabs-container="tools"] {
			&:before {
				background-image: linear-gradient(180deg, #826CE7 0%, #F9F9F9 100%); } }
		&[data-tabs-container="custodianship"] {
			&:before {
				background-image: linear-gradient(180deg, #7D89E9 0%, #F9F9F9 100%); } }
		&[data-tabs-container="electronic"] {
			&:before {
				background-image: linear-gradient(180deg, #78A7EB 0%, #F9F9F9 100%); } } } }

.abouttabs {
	.head {
		max-width: 600px;
		@include sm {
			max-width: 100%; } }
	&__left {
		margin-top: 173px;
		@include sm {
			margin-top: 0; } }
	&__list {
		@include sm {
			margin-bottom: 30px;
			display: flex;
			justify-content: center;
			flex-wrap: wrap; } }
	&__item {
		cursor: pointer;
		@include hss;
		opacity: .5;
		@include f(14px,700,1.6);
		transition: opacity .3s, color .3s;
		text-transform: uppercase;
		&:not(:last-child) {
			margin-bottom: 28px;
			@include sm {
				margin: 0 20px 5px 0; } }
		@include hover {
			opacity: 1;
			color: $red; }
		&.is-active {
			opacity: 1;
			color: $red; } }
	&__container {
		color: rgba($black, .6);
		h1,h2,h3,h4,h5,h6 {
			color: $black;
			&.color-red {
				color: $red; } }
		.title-md,
		.title-mid {
			&:not(:last-child) {
				margin-bottom: 35px;
				@include md {
					margin-bottom: 20px; } } }
		p {
			&:not(:last-child) {
				margin-bottom: 23px;
				@include md {
					margin-bottom: 15px; } } } } }

.list {
	&:not(:last-child) {
		margin-bottom: 23px; }
	li {
		position: relative;
		padding-left: 18px;
		display: block;
		&:before {
			position: absolute;
			content: '';
			top: 14px;
			left: 0;
			@include circle(8);
			background-color: $red; } } }
