.form-success {
	position: relative;
	@include fadeout(.2s);
	color: lighten(green, 10%);
	&.is-active {
		@include fadein; } }

.form {
	&.is-success {
		.btn.btn_black[type="submit"] {
			color: $black;
			&:before {
				@include fadein; } } }
	.btn.btn_black[type="submit"] {
		position: relative;
		&:before {
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			width: 18px;
			height: 13px;
			margin: -7px 0 0 -9px;
			@include fadeout(.3s);
			background-image: url(../img/check.svg);
			background-repeat: no-repeat;
			background-position: 0 0;
			background-size: contain; } }
	&__title {
		margin-bottom: 35px; }
	&__confirm {
		margin-top: 12px; }
	&__footer {
		position: relative;
		margin-top: 30px;
		.form-success {
			position: absolute;
			left: 0;
			top: 100%;
			margin-top: 5px;
			width: 100%; }
		.btn {
			@include sm {
				width: 100%; } } } }
.form-control {
	position: relative;
	&:not(:first-child) {
		margin-top: 20px; }
	input,
	textarea {
		padding: 6px 20px;
		width: 100%;
		height: 50px;
		@include rob;
		color: $black;
		@include f(16px,400,2.375);
		border: none;
		border-radius: 5px;
		display: block;
		transition: border-color .3s, color .3s;
		border: 1px solid #E0E0E0;
		background-color: $white;
		&::placeholder {
			color: rgba($black, .3); }
		&:focus {
			border-color: darken(#E0E0E0, 30%);
			&::placeholder {
				font-size: 0; } }
		@include hover {
			border-color: darken(#E0E0E0, 30%); }
		// &.is-filled
		// 	border-color: #E0E0E0
		&.error {
			color: $red;
			border-color: $red;
			&::placeholder {
				color: $red; } } }
	textarea {
		resize: none;
		height: 150px; } }

.form-error {
	margin-top: 10px;
	padding: 4px 10px;
	color: $red;
	text-align: left;
	@include f(16px,400,1.25);
	line-height: 1.2;
	border-radius: 5px;
	background-color: rgba($red, .1);
	display: block; }

.checkbox {
	position: relative;
	user-select: none;
	cursor: pointer;
	vertical-align: top;
	display: inline-block;
	input {
		position: absolute;
		visibility: hidden;
		opacity: 0;
		&:checked ~ .checkbox__text {
			&:after {
				transform: scale(1); } }
		&.error ~ .checkbox__text {
			&:before {
				border-color: $red; } } }
	& + .form-error {
		display: none !important; }
	&__text {
		position: relative;
		padding-left: 30px;
		color: rgba($black, .3);
		display: block;
		&:before {
			position: absolute;
			content: '';
			top: 9px;
			left: 0;
			border-radius: 5px;
			width: 20px;
			height: 20px;
			transition: border-color .3s;
			border: 1px solid transparent;
			background-color: $white; }
		&:after {
			position: absolute;
			content: '';
			top: 14px;
			left: 5px;
			width: 10px;
			height: 10px;
			transform: scale(0);
			transition: transform .3s;
			border-radius: 2px;
			background-color: $red; } } }
